                $('html').css({'overflow':'hidden'});
                $('.preloader-overlay').css({'display':'none'});
            }
            $('html').css({'overflow':'auto'});
            $('.box-span').css({'border-color':'white'});
            $('.preloader-overlay').css({'display':'none'});
            var menuItems = document.getElementsByClassName('left-menu-li');
            for (var i=0; i < 5; i++){
                if (i == 3){
                    menuItems[i].classList.add('menu-active');
