        <div class="asteroid"></div>
        <p class="cases-title">
            <span class="first-row">{{lang == 'ru' ? ru.cases1 : en.cases1}}<span class="yellow">{{lang == 'ru' ? ru.cases2 : en.cases2}}</span></span><br>
            <span class="second-row">{{lang == 'ru' ? ru.cases3 : en.cases3}}</span><br>
            <span class="third-row">{{lang == 'ru' ? ru.cases4 : en.cases4}}<span class="yellow">{{lang == 'ru' ? ru.cases5 : en.cases5}}</span></span>
        </p>
        <div class="cases-blocks">
            <div class="columns">
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/0'" v-on:click.native="savePosition()"><div class="case case1"></div></router-link>
                </div>
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/1'" v-on:click.native="savePosition()"><div class="case case2"></div></router-link>
                </div>
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/2'" v-on:click.native="savePosition()"><div class="case case3"></div></router-link>
                </div>
            </div>
            <div class="columns">
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/3'" v-on:click.native="savePosition()"><div class="case case4"></div></router-link>
                </div>
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/4'" v-on:click.native="savePosition()"><div class="case case5"></div></router-link>
                </div>
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/5'" v-on:click.native="savePosition()"><div class="case case6"></div></router-link>
                </div>
            </div>
            <div class="columns">
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/6'" v-on:click.native="savePosition()"><div class="case case8"></div></router-link>
                </div>
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/7'" v-on:click.native="savePosition()"><div class="case case7"></div></router-link>
                </div>
                <div class="column is-one-third">
                    <router-link :to="'/cases/'+lang+'/8'" v-on:click.native="savePosition()"><div class="case case9"></div></router-link>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        props:['lang'],
        data(){
            return{
                popupActive: false,
                index: 0,
                ru:{
                    cases1: ' ',
                    cases2: '   ',
                    cases3: '    ,  ,',
                    cases4: '    ',
                    cases5: ' ',
                },
                en:{
                    cases1: 'We create ideas',
                    cases2: ' ideas and successul cases',
                    cases3: 'They don\'t fall down like asteroids',
                    cases4: 'Because it is a result of our experience',
                    cases5: ' and hard work',
                },
            }
        },
        methods:{
            changeSlide : function (input) {
                if (((this.index + input) > -1) && ((this.index + input) < 4)) {
                    this.index = this.index + input;
                }
            },
            savePosition : function () {
                $('html').css({'overflow':'auto'});
                localStorage.pagePosition = 3;
                this.popupActive = true;
                $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //   
                $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                setInterval(function () {
                    $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //   
                    $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                    console.log('poo');
                }, 12000)
            }
        }
    }
</script>
<style scoped>
    @import "https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css";
