<template lang="html">
  <header>



    <div class="lang_switch">
        <span class="lang_switch_checkbox_label">{{(lang == 'ru') ? '' : 'English'}}</span>


        <label class="switch"><input @click="changeLang" type="checkbox" />    <div></div>
        </label>
    </div>

    <modal style="background-color: rgba(0,0,0,.9); z-index: 10000" name="blogger-form" id="blogger-form">
    <img class="astronaut" src="../assets/Gif/Astronaut.gif" alt="">
        <img class="blogger-form-planet" src="../assets/form-planet-blogger.png" alt="">
        <p class="blogger-form-title">
            <span class="yellow">{{lang == 'ru' ? ru.form_blogger_text_first : en.form_blogger_text_first}}</span><br>
            {{lang == 'ru' ? ru.form_blogger_text_second : en.form_blogger_text_second}} <span class="yellow">{{lang == 'ru' ? ru.form_blogger_text_third : en.form_blogger_text_third}}</span> {{lang == 'ru' ? ru.form_blogger_text_four : en.form_blogger_text_four}}<br>
            {{lang == 'ru' ? ru.form_blogger_text_five : en.form_blogger_text_five}} <span class="yellow">{{lang == 'ru' ? ru.form_blogger_text_six : en.form_blogger_text_six}}</span>
        </p>
        <form
          @submit="checkBloggerForm"
          method="post"
          novalidate="true"
        >

          <p>
            <input
              id="name"
              v-model="blogger_name"
              type="text"
              name="name"
              :placeholder="lang == 'ru' ? ru.form_name : en.form_name"
            >
           </p>

          <p>
            <input
              id="email"
              v-model="blogger_email"
              type="text"
              name="email"
              :placeholder="lang == 'ru' ? ru.form_email : en.form_email"
            >
          </p>

          <p>
            <input
              id="phoneNumber"
              v-model="blogger_phoneNumber"
              type="number"
              name="phoneNumber"
              :placeholder="lang == 'ru' ? ru.form_phone : en.form_phone"
            >
          </p>

          <p>
            <input
              id="link"
              v-model="blogger_link"
              type="text"
              name="link"
              :placeholder="lang == 'ru' ? ru.form_account_link : en.form_account_link"
            >
          </p>
          <button type="submit">Liftoff!</button>
        <p class="pin">{{lang == 'ru' ? ru.form_pin : en.form_pin}}</p>
        </form>

    </modal>

    <modal style="background-color: rgba(0,0,0,.9);" name="advertiser-form" id="advertiser-form">
      <img class="astronaut" src="../assets/Gif/Astronaut.gif" alt="">
      <img class="blogger-form-planet advertiser-planet" src="../assets/form-planet-advertiser.jpg" alt="">
        <p class="blogger-form-title">
            {{lang == 'ru' ? ru.form_advertiser_text_first : en.form_advertiser_text_first}} <span class="yellow">{{lang == 'ru' ? ru.form_advertiser_text_second : en.form_advertiser_text_second}}</span><br>
            <span class="yellow">{{lang == 'ru' ? ru.form_advertiser_text_third : en.form_advertiser_text_third}}</span>
        </p>
        <form
          @submit="checkAdviserForm"
          method="post"
          novalidate="true"
        >

          <p>
            <input
              id="name"
              v-model="advertiser_name"
              type="text"
              name="name"
              :placeholder="lang == 'ru' ? ru.form_name : en.form_name"
            >
          </p>

          <p>
            <input
              id="email"
              v-model="advertiser_email"
              type="text"
              name="email"
              :placeholder="lang == 'ru' ? ru.form_email : en.form_email"
            >
          </p>

          <p>
            <input
              id="phoneNumber"
              v-model="advertiser_phoneNumber"
              type="text"
              name="phoneNumber"
              :placeholder="lang == 'ru' ? ru.form_phone : en.form_phone"
            >
          </p>

          <p>
            <input
              id="startDate"
              v-model="advertiser_startDate"
              type="text"
              name="startDate"
              :placeholder="lang == 'ru' ? ru.form_start_date : en.form_start_date"
            >
          </p>

          <p>
            <input
              id="budget"
              v-model="advertiser_budget"
              type="number"
              name="budget"
              :placeholder="lang == 'ru' ? ru.form_budget : en.form_budget"
            >
          </p>
          <button type="submit">Liftoff!</button>

        </form>
    </modal>
    <div class="header-bg"></div>
    <div class="header-logo"></div>
    <span class="header-subtitle">{{lang == 'ru' ? ru.header_subtitle : en.header_subtitle}}</span>

    <transition-group name="btn-fade" mode="out-in">
        <div :key="!droped" v-show="!droped" class="cta-button-header" @click="animating()"><span class="cta-button-header-text">LIFTOFF!</span></div>
        <div :key="droped">
            <div @click="show(0)" v-show="droped" class="cta-button-header cta-button-header-blogger"><span class="cta-button-header-text" @click="animate()">{{lang == 'ru' ? ru.im_blogger : en.im_blogger}}</span></div>
            <div @click="show(1)" v-show="droped" class="cta-button-header cta-button-header-advertiser"><span class="cta-button-header-text cta-button-header-advertiser-text" @click="animate()">{{lang == 'ru' ? ru.im_advertiser : en.im_advertiser}}</span></div>
        </div>
       </transition-group>
  </header>
</template>

<script>
import axios from 'axios'

export default {
    data(){
        return{
            reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
            droped: false,
            blogger_name: '',
            blogger_email: '',
            blogger_phoneNumber: '',
            blogger_link: '',
            advertiser_name: '',
            advertiser_email: '',
            advertiser_phoneNumber: '',
            advertiser_budget: '',
            advertiser_startDate: '',
            lang: 'ru',
            ru:{
                header_subtitle: '   ',
                form_name: ' ',
                form_email: ' Email',
                form_phone: '',
                form_account_link: '  ',
                form_start_date: '  ?',
                form_budget: ' ',
                form_pin: '*   10 000 ',
                im_advertiser: ' ',
                im_blogger: ' ',
                form_blogger_text_first: '   !',
                form_blogger_text_second: '   ',
                form_blogger_text_third: '',
                form_blogger_text_four: ' ',
                form_blogger_text_five: ' -',
                form_blogger_text_six: '  !',
                form_advertiser_text_first: ' ',
                form_advertiser_text_second: ' ',
                form_advertiser_text_third: '  !',
            },
            en:{
                header_subtitle: 'FULL CYCLE ADVERTISING AGENCY',
                form_name: 'Your name',
                form_email: 'E-mail',
                form_phone: 'Phone number',
                form_account_link: 'Your channel link',
                form_start_date: 'when do you plan to liftoff?',
                form_budget: 'Your budget',
                form_pin: '*For bloggers from 10,000 subscribers',
                im_advertiser: 'I am advertiser' ,
                im_blogger: 'I am blogger',
                form_blogger_text_first: 'Invest your time in creativity!',
                form_blogger_text_second: 'trust us',
                form_blogger_text_third: 'the concern of attracting',
                form_blogger_text_four: 'advertisers',
                form_blogger_text_five: 'we are',
                form_blogger_text_six: 'your personal manager!',
                form_advertiser_text_first: 'Cosmic boost',
                form_advertiser_text_second: 'your conversion',
                form_advertiser_text_third: 'liftoff with us!',
            }
        }
    },

    mounted(){
        if (localStorage.lang){
            this.lang = localStorage.lang
        }
    },


    methods:{
        animating(){
            this.droped = true;
        },
        show(formNum) {
            if (formNum == 0){
                this.$modal.show('blogger-form');
            }else{
                this.$modal.show('advertiser-form');
            }

        },
        hide () {
            this.$modal.hide('blogger-form');
            this.$modal.show('advertiser-form');
        },

        changeLang(){

            if (localStorage.lang){

                if (localStorage.lang == 'ru'){
                    localStorage.lang = 'en';
                }else{
                    localStorage.lang = 'ru';
                }
            }else {
                localStorage.lang = 'ru'
            }
            this.lang = localStorage.lang;
            this.$emit('updateLang', this.lang);
        },
        isEmailValid: function(email) {
            return (email == ''? "" : (this.reg.test(email)) ? 'has-success' : 'has-error');
        },
        checkBloggerForm : function (e) {
            if (this.isEmailValid(this.blogger_email) == 'has-error'){
                this.blogger_email='  email'
            }else{
                axios.post('url/sendform.php', {
                    who: 'blogger',
                    name: this.blogger_name,
                    email: this.blogger_email,
                    phoneNumber: this.blogger_phoneNumber,
                    link: this.blogger_link

                })
                    .then(response => {})
                    .catch(err => {
                        this.errors.push(err)
                    })
            }

            e.preventDefault();
        },
        checkAdviserForm : function (e) {
            if (this.isEmailValid(this.advertiser_email) == 'has-error'){
                this.advertiser_email='  email'
            }else{
                axios.post('url/sendform.php', {
                    who: 'advertiser',
                    name: this.advertiser_name,
                    email: this.advertiser_email,
                    phoneNumber: this.advertiser_phoneNumber,
                    budget: this.advertiser_budget,
                    startDate: this.advertiser_startDate,
                })
                    .then(response => {})
                    .catch(err => {
                        this.errors.push(err)
                    })
            }

            e.preventDefault();
        },
    }
}
</script>

<style lang="css" scoped>
</style>
