                  if (localStorage.pagePosition){
                      e.data.pageIndex = localStorage.pagePosition;
                      localStorage.removeItem('pagePosition');
                  }

                  var wd = e.originalEvent.wheelDelta || -e.originalEvent.detail;

                  if (!scrollTimeout) {
                      scrollTimeout = setTimeout(function () {
                          if ((e.data.pageIndex == 0)){    // 1 
                              if (wd < 0){
                                  e.data.changeMenuItem(1);
                                  $('header').addClass('header_hidden');
                                  e.data.pageIndex++; //    . 

                                  if (e.data.pageIndex > slides.length-1){ //     
                                      e.data.pageIndex = slides.length-1;
                                  }
                              }else{
                                  e.data.pageIndex--; //  ,  wd > 0
                                  if (e.data.pageIndex < 0){ //     
                                      e.data.pageIndex = 0;
                                  }
                              }
                              if ((e.data.pageIndex == 1)){
                                  e.data.changePage(5000, e.data.pageIndex); //  1  2 
                              }else{
                                  e.data.changePage(1000, e.data.pageIndex);
                              }
                          } else if (e.data.pageIndex == 1){ //  2 
                              $(".first-row").css({'margin-left':'-200vw'}); //   4 
                              $(".second-row").css({'margin-right':'-200vw'}); //   4 
                              $(".third-row").css({'margin-left':'-200vw'}); //   4 
                              if (wd < 0){
                                  e.data.pageIndex++; //    . 
                                  e.data.changePage(1000, e.data.pageIndex);
                                  e.data.changeMenuItem(2);
                                  $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //   
                                  $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                                  setInterval(function () {
                                      $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //   
                                      $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                                  }, 8000)
                              }else{
                                  e.data.pageIndex--; //  ,  wd > 0
                                  e.data.changePage(1000, e.data.pageIndex);
                                  e.data.changeMenuItem(0);
                                  $('header').removeClass('header_hidden');
                              }
//                              var result = e.data.isSecondSlide(e.data.pageIndex, wd); //    2 
//                              if (result == 'nextPage'){
//                                  e.data.changeMenuItem(2);
//                                  e.data.pageIndex++; //  
//                                  e.data.changePage(1000, e.data.pageIndex); //   3 


//                              }else if (result == 'prevPage'){
//                                  e.data.changeMenuItem(0)
//                                  $('header').removeClass('header_hidden');
//                                  e.data.pageIndex--; //  
//                                  e.data.changePage(1000, e.data.pageIndex); //  2  1 
//                              }


