                              if (wd < 0){
                                  e.data.changeMenuItem(4)
                                  e.data.pageIndex++;
                                  e.data.changePage(1000, 1, e.data.pageIndex); //  5 
                              }else{
                                  e.data.changeMenuItem(2)
                                  e.data.pageIndex--;
                                  e.data.changePage(1000, e.data.pageIndex); //   3 

                              }
                          }
                          else if (e.data.pageIndex == 4) {
                              if (wd > 0){
                                  e.data.changeMenuItem(3)
                                  e.data.pageIndex--;
                                  e.data.changePage(1000, e.data.pageIndex); //  4 
                              }
                          }

                          scrollTimeout = null;
                      }, throttle);
                  }

              }

          });
      }else{
          window.onload=function(){
              $('.preloader-overlay').css({'display':'none'});
              $('html').css({'overflow':'auto'});
          }
      }

  },
  methods: {
      animating(){
          this.droped = true;
      },
      showFooter(formNum) {
          if (formNum == 0){
              this.$modal.show('blogger-form-footer');
          }else{
              this.$modal.show('advertiser-form-footer');
          }

      },
      hide () {
          this.$modal.hide('blogger-form-footer');
          this.$modal.show('advertiser-form-footer');
      },
      initParticlesJS () {
       /* eslint-disable */
       particlesJS('particles-js', {
         'particles': {
           'number': {
             'value': 60,
             'density': {
               'enable': true,
               'value_area': 800
             }
           },
           'color': {
             'value': '#ffffff'
           },
           'shape': {
             'type': 'circle',
             'stroke': {
               'width': 0,
               'color': '#000000'
             },
             'polygon': {
               'nb_sides': 5
             },
             'image': {
               'src': 'img/github.svg',
               'width': 100,
               'height': 100
             }
           },
           'opacity': {
             'value': 0.5,
             'random': false,
             'anim': {
               'enable': false,
               'speed': 1,
               'opacity_min': 0.1,
               'sync': false
             }
           },
           'size': {
             'value': 3,
             'random': true,
             'anim': {
               'enable': false,
               'speed': 20,
               'size_min': 0.1,
               'sync': false
             }
           },
           'line_linked': {
             'enable': false,
             'distance': 300,
             'color': '#ffffff',
             'opacity': 0.4,
             'width': 2
           },
           'move': {
             'enable': true,
             'speed': 1,
             'direction': 'none',
             'random': false,
             'straight': false,
             'out_mode': 'out',
             'bounce': false,
             'attract': {
               'enable': false,
               'rotateX': 600,
               'rotateY': 1200
             }
           }
         },
         'interactivity': {
           'detect_on': 'canvas',
           'events': {
             'onhover': {
               'enable': false,
               'mode': 'repulse'
             },
             'onclick': {
               'enable': false,
               'mode': 'push'
             },
             'resize': true
           },
           'modes': {
             'grab': {
               'distance': 800,
               'line_linked': {
                 'opacity': 1
               }
             },
             'bubble': {
               'distance': 800,
               'size': 80,
               'duration': 2,
               'opacity': 0.8,
               'speed': 3
             },
             'repulse': {
               'distance': 400,
               'duration': 0.4
             },
             'push': {
               'particles_nb': 4
             },
             'remove': {
               'particles_nb': 2
             }
           }
         },
         'retina_detect': true
       })
     },
      changeSlide : function (input) {
          if (((this.index + input) > -1) && ((this.index + input) < 7)) {
              this.index = this.index + input;
          }
      },
      changePage : function (time, index, inc) {
          var slides = document.getElementsByClassName('contslide');
          inc = inc || 0;
          $('body').addClass('fired');

          $('html, body').animate({
              scrollTop: slides[index + inc].offsetTop,
          }, {
              duration: time, //2500
          });
          setTimeout(function () {
              $('body').removeClass('fired');
          }, time)

      },
      changeMenuItem : function(num){
          var menuItems = document.getElementsByClassName('left-menu-li');
          for (var i=0; i < 5; i++){
              if (i == num){
                  menuItems[i].classList.add('menu-active');
              }else{
                  menuItems[i].classList.remove('menu-active');
              }
          }
      },
      openContacts : function () {
          this.isContactOpened ? $('footer').removeClass('animate') : $('footer').addClass('animate');
          this.isContactOpened = !this.isContactOpened;
      },
      goToElement : function (num) {

          if (num == 4){
              this.changePage(1000, num + 1);
              localStorage.pagePosition = num;
              this.changeMenuItem(num);
          }else if (num == 1){
              this.changePage(5000, num);
              localStorage.pagePosition = num;
              this.changeMenuItem(num);
          } else {
              this.changePage(1000, num);
              localStorage.pagePosition = num;
              this.changeMenuItem(num);
              if (num == 0){
                  $('header').removeClass('header_hidden');
              }
              if (num == 2){
                  $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //   
                  $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                  setInterval(function () {
                      $("#meteor").animate({width: "40%", left: "100vw", top: "25vh"}, {duration: 10000}); //   
                      $("#meteor").animate({left:'-20vw', width:'20%'}, {duration: 0});
                  }, 8000)
              }
              if (num == 3){
                  setTimeout(function () {
                      $(".first-row").animate({marginLeft: "0"}, {duration: 1500}); //     4 
                  }, 100);
                  setTimeout(function () {
                      $(".second-row").animate({marginRight: "0"}, {duration: 1500}); //     4 
                  }, 100);
                  setTimeout(function () {
                      $(".third-row").animate({marginLeft: "0"}, {duration: 1500}); //     4 
                  }, 100);
              }
          }
      },
      isSecondSlide : function (index, wd) {
          if (index == 1){

              if (wd < 0) {
                  this.k++;
                  this.changeSlide(1);
                  if (this.k == 7){
                      this.k = 6;
                      return 'nextPage';
                  }
              }
              else {
                  this.k--;
                  this.changeSlide(-1);
                  if (this.index == 0){
                      return 'prevPage';
                  }
              }
          }
      },
      isThirdSlide : function (index, wd) {
          if (index == 2){
              if (wd < 0) {
                  return 'nextPage';
              }
          }
      },
      isEmailValid: function(email) {
          return (email == ''? "" : (this.reg.test(email)) ? 'has-success' : 'has-error');
      },
      checkBloggerForm : function (e) {
        if (this.isEmailValid(this.blogger_email) == 'has-error'){
            this.blogger_email='  email'
        }else{
            axios.post('url/sendform.php', {
                who: 'blogger',
                name: this.blogger_name,
                email: this.blogger_email,
                phoneNumber: this.blogger_phoneNumber,
                link: this.blogger_link

            })
                .then(response => {})
                .catch(e => {
                    this.errors.push(e)
                })
        }

        e.preventDefault();
      },
      checkAdviserForm : function (e) {
          if (this.isEmailValid(this.adviser_email) == 'has-error'){
              this.adviser_email='  email'
          }else{
              axios.post('url/sendform.php', {
                  who: 'advertiser',
                  name: this.adviser_name,
                  email: this.adviser_email,
                  phoneNumber: this.adviser_phoneNumber,
                  budget: this.adviser_budget,
                  startDate: this.adviser_startDate,
              })
                  .then(response => {})
                  .catch(e => {
                      this.errors.push(e)
                  })
          }

          e.preventDefault();
      },
   }
}
</script>

